import Leaderboard from './Leaderboard';
import Navbar from './Navbar';
import Player from './Player';
import Home from './Home';
import MatchHistory from './Match-History';
import GameCompanion from './Game-Companion';
import React, { useState } from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
let config = require('./config.json')
let set1 = require('./set1.json');
let set2 = require('./set2.json');
let set3 = require('./set3.json');

const APIKEY = config.MY_API_TOKEN;

const CARDS = set1.concat(set2, set3);
const CARDCODE = CARDS.map(x => x.cardCode);
const PORT = '21337';
let r = Math.floor(Math.random() * CARDS.length);

let background = CARDS[r].assets[0].fullAbsolutePath;

function App() {

  return (
    <Router>
      <Navbar />
      <div style={{
        backgroundImage: `url(${background})`,
        minHeight: '100vh',
        backgroundPosition: `center`
      }} class='bg-dark text-light container-fluid pt-3'>
        <Switch>
          <Route exact path="/"  component={Home} />
          <Route path="/leaderboard" component={Leaderboard} />
          <Route exact path="/history"  component={MatchHistory} />
          <Route path="/history/profile/:server/:name/:tagline/:matches" component={Player} />
          <Route path="/companion" component={GameCompanion} />
        </Switch>
      </div>
    </Router>
  );
}


export default App;
